.hero-main {
    background: url(../../components/assets/images/banner-bg.jpg) no-repeat top right/cover;
    background-color: var(--color-dark);
    color: var(--color-light);
    border-radius: var(--boder-radius-primary);
    padding: var(--padding-primary);
    
}

.hero-text{
    max-width: 500px;
}

.hero-title {
    font-size: 60px;
    text-transform: uppercase;
    margin-bottom: calc(var( --distance-primary)/2);
    font-weight:var(--font-weight-bold);
}

.hero-title  em {
    font-style: normal;
    color: var(--color-primary);
}

.hero-subtitle {
    font-size: 20px;
    color: white;
    font-weight: var(--font-weight-noraml);
    margin-bottom: calc(var(--distance-primary)/2);
}

